import { select } from "d3-selection";
import { injectCSS, css_string } from "./css.js";

var control_types = Object.freeze(["auto-buttons", "grouped-buttons", "floating-buttons", "dropdown", "slider", "none"]);

function isFiniteNumber(val) { return typeof val === "number" && isFinite(val); }


function createCore(container_selector) {
	injectCSS(css_string);

	var output_obj = {};

	var props = {
		"slider_step_time": 2,
		"slider_loop": true,
		"slider_loop_pause": 0,
		"slider_play_button": true,
		"slider_handle_colour": "#333",
		"slider_label": true,
		dropdown_icon_colour: "#333333"
	};

	Object.keys(props).forEach(function(key) {
		// Convert from snake case properties to camel case methods
		var methodName = key.replace(/_\w/g, function(substr) { return substr.charAt(1).toUpperCase(); });
		output_obj[methodName] = function(val) {
			if (val === undefined) return props[key];
			props[key] = val;
			return output_obj;
		};
	});

	props.type = "auto_buttons";

	output_obj.type = function(type) {
		if (type === undefined) return props.type;
		props.type = control_types.indexOf(type) !== -1 ? type : "auto-buttons";
		return output_obj;
	};

	Object.defineProperty(props, "control", {
		get: function() {
			var type = props.type;
			return (type === "dropdown" || type === "slider" || type === "none") ? props.type : "buttons";
		},
		set: function() { console.warn("Nothing changed: control is read only"); }
	});

	Object.defineProperty(props, "buttons_type", {
		get: function() {
			if (props.control !== "buttons") return undefined;
			if (props.type === "auto-buttons") return "auto";
			if (props.type === "grouped-buttons") return "grouped";
			return "floating";
		},
		set: function() { console.warn("Nothing changed: button_type is read only"); }
	});

	props.index = 0;

	output_obj.index = function(index) {
		if (index === undefined) return props.n_options ? props.index : null;
		if (index !== null) {
			if (!isFiniteNumber(index)) throw TypeError("index must be a number greater than or equal to 0.");
			if (index < 0) throw RangeError("index must be greater than or equal to 0.");
			props.index = Math.floor(index);
		}
		return output_obj;
	};

	props.options = [];

	Object.defineProperty(props, "n_options", {
		get: function() { return props.options.length; },
		set: function() { console.warn("Nothing changed: n_options is read only"); }
	});

	output_obj.options = function(opts) {
		if (opts === undefined) return props.options.slice();
		if (!Array.isArray(opts)) throw TypeError("opts must be an array.");
		props.options = Object.freeze(opts.slice());
		return output_obj;
	};

	Object.defineProperty(props, "value", {
		get: function() { return props.n_options ? props.options[props.index] : null; },
		set: function() { console.warn("Nothing changed: value is read only"); }
	});

	output_obj.value = function(value) {
		if (value === undefined) return props.value;
		if (value !== null) {
			var index = props.options.indexOf(value);
			if (index !== -1) props.index = index;
			else console.warn("Nothing changed: value not in options list");
		}
		return output_obj;
	};

	Object.defineProperty(props, "width", {
		get: function() { return container_node.getBoundingClientRect().width + "px"; },
		set: function() { console.warn("Nothing changed: width is read only"); }
	});

	props.changeHandlers = [];

	output_obj.on = function(event, callback) {
		if (event === "change") props.changeHandlers.push(callback.bind(output_obj));
		return output_obj;
	};

	var onChangeCallbacks = function() {
		var value = props.value;
		var index = props.index;
		props.changeHandlers.forEach(function(func) {
			func(value, index);
		});
	};

	Object.seal(props);

	// Grab container
	var container = select(container_selector);
	var added_class;
	if (!container.classed("controls-container")) {
		container.classed("controls-container", true);
		added_class = true;
	}
	else added_class = false;
	var container_node = container.node();
	output_obj.container = container;

	return {
		output_obj: output_obj,
		props: props,
		onChangeCallbacks: onChangeCallbacks,
		container: container,
		container_node: container_node,
		container_selector: container_selector,
		added_class: added_class
	};
}

export { createCore };
