// https://stackoverflow.com/a/21626701
function makeBackgroundString(svg_string) {
	return "url('data:image/svg+xml;base64," + window.btoa(svg_string) + "')";
}

var css_injected = false;

function injectCSS(css) {
	if (css_injected || typeof document === "undefined") return;
	var head = document.head || document.getElementsByTagName("head")[0];
	var style = document.createElement("style");
	style.type = "text/css";
	head.appendChild(style);

	if (style.styleSheet) {
		style.styleSheet.cssText = css;
	}
	else {
		style.appendChild(document.createTextNode(css));
	}
	css_injected = true;
}

export { injectCSS, css_string, makeBackgroundString };
