function isUrl(string) {
	if (typeof string != "string") return false;
	return string.match(/^https?:\/\//i) != null;
}

function isImage(string) {
	if (typeof string != "string") return false;
	return string.match(/^https?:\/\/.+\.(jpg|jpeg|svg|png|gif|webp)$/i) != null;
}

export { isUrl, isImage }